.SentenceGame {
  text-align: center;
  width: 1000px; /* Increase the fixed width */
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Distribute elements evenly */
  align-items: center; /* Center align elements */
  margin: 0 auto; /* Center the box */
  max-height: 90vh; /* Set maximum height */
  overflow-y: auto; /* Handle vertical overflow */
}

.sentence-box {
  margin: 20px 0;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #f9f9f9;
  width: 100%; /* Fixed width */
  max-width: 800px; /* Increase maximum width */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.meaning {
  font-size: 32px;
  font-weight: bold;
  color: #333;
  word-break: break-word; /* Break long words */
}

.translation {
  margin-top: 10px;
  font-size: 16px;
  color: #555;
  word-break: break-word; /* Break long words */
}

input {
  width: 100%;
  max-width: 800px; /* Increase maximum width */
  padding: 15px;
  font-size: 18px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1) inset;
}

.button-container {
  display: flex;
  gap: 10px; /* Add space between buttons */
  justify-content: center;
  margin-top: 20px;
}

button {
  width: 150px;
  padding: 15px;
  font-size: 18px;
  background: #28a745; /* Default green color */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

button:hover {
  background: #218838; /* Darker green on hover */
}

button.task-button {
  background: #007bff; /* Blue color for Task 선택 */
}

button.task-button:hover {
  background: #0056b3; /* Darker blue on hover */
}

.score {
  font-size: 20px;
  color: #333;
}

.loading, .game-over {
  text-align: center;
  font-size: 24px;
  color: #555;
}

/* Overlay styles */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay-content {
  background: white;
  padding: 30px;
  border-radius: 10px;
  text-align: center;
}

.overlay-content p {
  font-size: 24px;
  margin-bottom: 20px;
}

.overlay-content button {
  padding: 10px 20px;
  font-size: 18px;
  background: #dc3545; /* Change to red color */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.overlay-content button:hover {
  background: #c82333; /* Change to red color */
}

/* Progress bar styles */
.progress-container {
  width: 100%; /* Fixed width */
  max-width: 800px; /* Increase maximum width */
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.progress-text {
  font-size: 18px;
  margin-bottom: 5px;
}

.progress-bar {
  width: 100%;
  background-color: #e0e0e0;
  border-radius: 5px;
  overflow: hidden;
  height: 20px;
}

.progress {
  height: 100%;
  background-color: #28a745;
  transition: width 0.3s;
}

.game-over {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 1000px; /* Increase fixed width */
  height: 700px; /* Fixed height */
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  max-height: 90vh; /* Set maximum height */
  overflow-y: auto; /* Handle vertical overflow */
}

/* Align incorrect sentences list to the left and make it scrollable */
.game-over ul {
  text-align: left; /* Align left */
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove padding */
  max-height: 200px; /* Set maximum height */
  overflow-y: auto; /* Add vertical scroll */
  margin: 0; /* Remove default margin */
}

/* Style incorrect sentences list items */
.game-over ul li {
  margin-bottom: 10px; /* Space between items */
}
