.WordGame {
  text-align: center;
  width: 600px; /* 고정 너비 */
  height: 500px; /* 고정 높이 */
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* 요소들을 고르게 분배 */
  align-items: center; /* 수직 가운데 정렬 */
  margin: 0 auto; /* 가운데 정렬 */
}

.word-box {
  margin: 20px 0;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #f9f9f9;
  height: 200px; /* 고정 높이 */
  width: 100%; /* 고정 너비 */
  max-width: 400px; /* 최대 너비 설정 */
  display: flex;
  justify-content: center;
  align-items: center;
}

.meaning {
  font-size: 32px;
  font-weight: bold;
  color: #333;
}

input {
  width: 100%;
  max-width: 400px; /* 최대 너비 설정 */
  padding: 15px;
  font-size: 18px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1) inset;
}

/* Existing styles... */

.button-container {
  display: flex;
  gap: 10px; /* Add space between buttons */
  justify-content: center;
  margin-top: 20px;
}

button {
  width: 150px;
  padding: 15px;
  font-size: 18px;
  background: #28a745; /* Default green color */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

button:hover {
  background: #218838; /* Darker green on hover */
}

button.task-button {
  background: #007bff; /* Blue color for Task 선택 */
}

button.task-button:hover {
  background: #0056b3; /* Darker blue on hover */
}

.score {
  font-size: 20px;
  color: #333;
}

.loading, .game-over {
  text-align: center;
  font-size: 24px;
  color: #555;
}

/* Overlay styles */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay-content {
  background: white;
  padding: 30px;
  border-radius: 10px;
  text-align: center;
}

.overlay-content p {
  font-size: 24px;
  margin-bottom: 20px;
}

.overlay-content button {
  padding: 10px 20px;
  font-size: 18px;
  background: #dc3545; /* 붉은색 계열로 변경 */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.overlay-content button:hover {
  background: #c82333; /* 붉은색 계열로 변경 */
}

/* Progress bar styles */
.progress-container {
  width: 100%; /* 고정 너비 */
  max-width: 400px; /* 최대 너비 설정 */
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.progress-text {
  font-size: 18px;
  margin-bottom: 5px;
}

.progress-bar {
  width: 100%;
  background-color: #e0e0e0;
  border-radius: 5px;
  overflow: hidden;
  height: 20px;
}

.progress {
  height: 100%;
  background-color: #28a745;
  transition: width 0.3s;
}

.game-over {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 600px; /* 고정 너비 */
  height: 700px; /* 고정 높이 */
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/* 틀린 단어 목록 좌측 정렬 및 스크롤 가능 */
.game-over ul {
  text-align: left; /* 좌측 정렬 */
  list-style-type: none; /* 불릿 제거 */
  padding: 0; /* 패딩 제거 */
  max-height: 200px; /* 최대 높이 설정 */
  overflow-y: auto; /* 수직 스크롤 추가 */
  margin: 0; /* 기본 마진 제거 */
}

/* 틀린 단어 목록 아이템 스타일 */
.game-over ul li {
  margin-bottom: 10px; /* 아이템 간 간격 */
}

