.WordGame {
  text-align: center;
  width: 600px;
  height: 600px;
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.word-box {
  margin: 20px 0;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #f9f9f9;
  height: 150px;
  width: 100%;
  max-width: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.meaning {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

.options-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.options-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.option-button {
  padding: 10px;
  font-size: 18px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.option-button:hover {
  background: #0056b3;
}

.score {
  font-size: 20px;
  color: #333;
}

.loading, .game-over {
  text-align: center;
  font-size: 24px;
  color: #555;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay-content {
  background: white;
  padding: 30px;
  border-radius: 10px;
  text-align: center;
}

.overlay-content p {
  font-size: 24px;
  margin-bottom: 20px;
}

.overlay-content button {
  padding: 10px 20px;
  font-size: 18px;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.overlay-content button:hover {
  background: #c82333;
}

.progress-container {
  width: 100%;
  max-width: 400px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.progress-text {
  font-size: 18px;
  margin-bottom: 5px;
}

.progress-bar {
  width: 100%;
  background-color: #e0e0e0;
  border-radius: 5px;
  overflow: hidden;
  height: 20px;
}

.progress {
  height: 100%;
  background-color: #28a745;
  transition: width 0.3s;
}

.game-over {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 600px;
  height: 700px;
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.game-over ul {
  text-align: left;
  list-style-type: none;
  padding: 0;
  max-height: 200px;
  overflow-y: auto;
  margin: 0;
}

.game-over ul li {
  margin-bottom: 10px;
}
